.header {
	margin-bottom: 20px;
}

div.tableHead {
	margin-top: 20px;
	background-color: #f6f8fa;
	height: 40px;
	border: solid 1px #d7d9db;
	align-items: center;
}

.text {
	padding-left: 10px;
}

.row {
	padding: 15px;
	word-break: break-word;
	.text;
}

.rowContainer {
	border-bottom: solid 1px #d7d9db;
	border-left: solid 1px #d7d9db;
	border-right: solid 1px #d7d9db;
}

.rowCenter {
	display: flex;
	align-items: center;
}

.cicleItem {
	padding: 4px;
	margin: 4px 2px 0 2px;
	display: inline-block;
	background-color: #f0f0f5;
	color: #c41d7f;
	max-width: 100%;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}
