.cell {
    background: navajowhite;
    border-bottom: 1px solid #ccc;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
}
.autoSize {
    height: 300px;
    &.noSelect {
        * {
            user-select: none;
        }
    }
}

.header {
    font-size: 13px;
    background-color: #f6f8fa;
   
    .headerCell {
        display: flex;
        align-items: center;
        // padding: 0 9px;
        color: #6c737b;
        box-sizing: border-box;
      
    }
    &::-webkit-scrollbar {
        display: none;
    }
}
.cellStyle {
    display: flex;
    align-items: center;
    padding: 0 9px;
    color: #6c737b;
    border-bottom: solid 1px #ebedf0;
    box-sizing: border-box;
    color: #0f0f0f;
    font-size: 13px;
    &:hover {
        // background: #f3f3f6;
    }
   
}
.hover {
    background: #f3f3f6;
}

.row {
    padding: 0 9px ;
    position: relative;
    box-sizing: border-box;
}

.contentGrid {
    overflow-y: overlay !important;
}
.loadMore {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #aeafb5;
    font-size: 12px;
}

.loadMoreLoading {
    width: 16px;
    height: 16px;
    fill: palegreen;
}

.helpClass {
    background: #f3f3f6;
}
.headerTable {
    position: absolute;
    z-index: 1;
    background: #fff;
    :global {
        .ReactVirtualized__Grid {
            &::-webkit-scrollbar {
                display: none;
            }
        }
    }
}

.line {
    position:absolute;
    // border-left: 1px solid #f3f3f6;
    height: 100%;
    right: 0;
    // box-shadow: inset -6px 0 6px -4px #dfdfdf;
    box-shadow: inset 6px 0 6px -4px #dfdfdf;
    top: 0;
    width: 6px;
    z-index: 9999;
}
.rightHeaderTable {
    position: absolute;
    right: 0;
    z-index: 1;
    top: 0;
    background: #fff;
    :global {
        .ReactVirtualized__Grid {
            &::-webkit-scrollbar {
                display: none;
            }
        }
    }

    .rightLine {
        position:absolute;
        // border-left: 1px solid #f3f3f6;
        height: 100%;
        // right: 0;
        box-shadow: inset -6px 0 6px -4px #dfdfdf;
        // box-shadow: inset 6px 0 6px -4px #dfdfdf;
        top: 0;
        width: 6px;
        z-index: 9999;
    }
}

.border {
    // background-color: #e1e4e7;
    // content: " ";
    // height: 100%;
    // left: 1px;
    // position: absolute;
    // top: 0;
    // width: 1px;
    // height: 16px;
    // margin-top: -8px;
    // padding: 0 1px;
    // position: absolute;
    // right: .5px;
    // top: 50%;
    // width: 1px;
    // background-color: #e1e4e7;
    height: 16px;
    margin-top: -8px;
    padding: 0 1px;
    position: absolute;
    right: 0px;
    top: 50%;
    width: 4px;
    &::after {
        background-color: #e1e4e7;
    content: " ";
    height: 100%;
    left: 1px;
    position: absolute;
    top: 0;
    width: 1px;
    }
}

.startLine {
    position: absolute;
    top: 0;
    width: 1px;
    height: 100%;
    background-color: #2F85F9;
    z-index: 10;

    &::after {
        content: ' ';
        position: absolute;
        top: 0;
        width: 7px;
        height: 30px;
        left: -3px;
        background-color: #2F85F9;
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;
    }
}

.dragLine {
    position: absolute;
    top: 0;
    width: 3px;
    height: 100%;
    border-left: dashed 1px #2F85F9;
    z-index: 10;
    cursor: col-resize;
}
